    .container {
        .m-left {
            // width: 1200px;
            h2 {
                font-size: 24px;
                font-weight: normal;
                // width: 980px;
                height: 34px;
                span {
                    color: #3e3e3e;
                    border-bottom: 2px solid #ed593f;
                    padding-bottom: 6px;
                    padding-left: 20px;
                    padding-right: 20px;
                }
            }
            .bottom-line {
                border-bottom: 1px solid #7d7d7d;
                .text {
                    font-size: 16px;
                    border: none;
                    padding-left: 42px;
                }
                .pages {
                    float: right;
                    font-size: 16px;
                    margin: 10px;
                    span {
                        border: none;
                    }
                }
            }
            // 检索部分
            .search-area {
                width: 1150px;
                padding-bottom: 50px;
                .search-active {
                    background-color: #ed593f;
                    color: #fff;
                }
                .search-area-active {
                    background-color: #ed593f;
                    color: #fff;
                }
                .search-company-active {
                    background-color: #ed593f;
                    color: #fff;
                }
                .present-city,
                .house-type,
                .district-serve,
                .price-range,
                .service-class {
                    padding-top: 25px;
                    width: 100%;
                    font-size: 16px;
                    div {
                        float: left;
                        height: 30px;
                        text-align: center;
                        padding: 0 12px 0 5px;
                        line-height: 30px;
                        color: #3e3e3e;
                    }
                    ul {
                        float: left;
                        width: 1050px;
                        li {
                            float: left;
                            margin-bottom: 10px;
                            padding: 7px 18px;
                            cursor: pointer;
                            &:hover {
                                box-shadow: 1px 1px 8px 0 rgba(0, 0, 0, 0.6);
                            }
                        }
                        .active {
                            background-color: #ed593f;
                            color: #fff;
                        }
                    }
                }
            }
            // 公司列表部分
            .company-list {
                width: 1100px;
                // margin: 0 auto;
                margin-left: 20px;
                min-height: 600px;
                margin-top: 70px;
                // padding-top: 70px;
                // padding-left: 45px;
                ul {
                    width: 100%;
                    min-height: 600px;
                    // background-color: #f1f1f1;
                    // transition: all .4s;
                    li:hover {
                        transform: translate(0, -5px);
                    }
                    li {
                        width: 1000px;
                        height: 200px;
                        // background-color: #ccc;
                        margin-bottom: 80px;
                        box-shadow: 2px 2px 6px 2px #e9e9e9;
                        transition: all .4s;
                        cursor: pointer;
                        .m-left {
                            width: 200px;
                            height: 200px;
                            float: left;
                            .company-logo {
                                display: block;
                                width: 180px;
                                height: 180px;
                                margin: 10px;
                                // background-color: rgb(238, 238, 238);
                                background: #fff url(../images/public/loading5.gif) 0px 20px no-repeat;
                                background-size: 100%;
                            }
                        }
                        .m-middle {
                            float: left;
                            margin-left: 30px;
                            width: 600px;
                            height: 200px;
                            h3 {
                                margin-top: 10px;
                                font-size: 28px;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                                height: 30px;
                            }
                            .company {
                                margin-top: 20px;
                                font-size: 20px;
                            }
                            .tips {
                                margin-top: 30px;
                                padding: 0;
                                span {
                                    font-size: 14px;
                                    padding: 5px 15px;
                                    border-radius: 20px;
                                    color: #fff;
                                    vertical-align: bottom;
                                    margin-right: 10px;
                                }
                                .type {
                                    background-color: #ed593f;
                                }
                                .copy {
                                    background-color: #6fbdee;
                                }
                            }
                            .price {
                                margin-top: 30px;
                                font-size: 20px;
                                .price-num {
                                    font-size: 26px;
                                    color: #ed593f;
                                }
                                .service-num {
                                    font-size: 14px;
                                    margin-left: 15px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }